<template>
	<div class="privilegeList">
        <van-nav-bar
            title="特权列表"
            leftText="返回"
            leftArrow
            @click-left="onClickLeft"
        />

        <van-pull-refresh loosingText="松开立即刷新" v-model="isLoading" slot="loosing">
            <div class="list-content">
                <div class="card">
                    <div class="card-left">
                        <em>豪</em><em>车</em>更多
                    </div>
                    <div class="card-right">
                        <img src="./images/img1.png" alt="">
                        <h6>特权局请你开跑车</h6>
                        <p>敞篷保时捷首日免费</p>
                    </div>
                </div>
                <div class="card">
                    <div class="card-left">
                        <em class="darkblue">游</em><em class="darkblue">艇</em>更多
                    </div>
                    <div class="card-right">
                        <img src="./images/img2.png" alt="">
                        <h6>特权局请你开跑车</h6>
                        <p>敞篷保时捷首日免费</p>
                    </div>
                </div>
                <div class="card">
                    <div class="card-left">
                        <em class="purple">服</em><em class="purple">饰</em>更多
                    </div>
                    <div class="card-right">
                        <img src="./images/img3.png" alt="">
                        <h6>特权局请你开跑车</h6>
                        <p>敞篷保时捷首日免费</p>
                    </div>
                </div>
                <p class="finishLoad">已全部加载完毕</p>
            </div>
            <div class="loosing" slot="loosing">
                <img src="./images/img_arrow.png" alt="">
                <div class="loosing-word">
                    <p>松开立即刷新</p>
                    <p>最后更新：今天18:00</p>
                </div>
            </div>
        </van-pull-refresh>
	</div>
</template>

<script>
import 'lib-flexible'
import { Toast } from 'vant'
export default {
	name: 'PrivilegeList',
	data () {
		return {
            imageURL: '../img1.jpg',
            isLoading: false,
            pulling: 'SBSBSBSBSBSBSB'
		}
	},
	components: {
        [Toast.name]: Toast
        
    },
    methods: {
        onClickLeft() {
        
        }
    },
    watch: {
        isLoading() {
            if (this.isLoading) {
                setTimeout(() => {
                Toast('刷新成功');
                this.isLoading = false;
                }, 500);
            }
        }
    }
}
</script>

<style scoped  lang="less">
.privilegeList{
    background: #f6f6f6;
    width: 100%;
    .van-nav-bar{
        width: 100%;
        height: 88px;
        background: url(./images/title.png) no-repeat;
        background-size: 100%;
        font-size: 36px;
        color: #fff;
        .van-nav-bar__left{
            .van-nav-bar__text{
                color: #fff;
                font-size: 36px;
            }
        }
    }
    .list-content{
        margin-top: 40px;
        width: 100%;
        .card{
            display: flex;
            padding: 35px 0;
            margin-bottom: 40px;
            width: 100%;
            height: 570px;
            background: #fff;
            box-sizing: border-box;
            .card-left{
                @titHei: 114px;
                padding: 0 35px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                width: 125px;
                height: calc(~"100% - @{titHei}");
                text-align: center;
                font-size: 28px;
                color: #000;
                box-sizing: border-box;
                em{
                    margin-top: 4px;
                    margin-left: 9px;
                    display: inline-block;
                    width: 40px;
                    height: 40px;
                    line-height: 40px;
                    background: #000;
                    color: #fff;
                    text-align: center;
                    font-size: 36px;
                    &.darkblue{
                        background: #23418a;
                    }
                    &.purple{
                        background: #6d10ae;
                    }
                }
            }
            .card-right{
                flex: 1;
                img{
                    width: 583px;
                }
                h6{
                    font-size: 32px;
                    line-height: 1.9 ;
                    color: #000;
                }
                p{
                    font-size: 30px;
                    color: #919191;
                    line-height: 1.3;
                }
            }
        }
        .finishLoad{
            padding: 30px 0;
            font-size: 28px;
            color: #5a5a5a;
            text-align: center;
        }
    }
    .loosing{
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 333px;
        >img{
            width: 28px;
            height: 50px;
        }
        .loosing-word{
            p{
                text-align: center;
                font-size: 28px;
                color: #5a5a5a;
                line-height: 1.5;
            }
        }
    }
}
</style>



